:root {
    -webkit-user-select: none;
            user-select: none;
    overflow-x: hidden;
    --btn-background: #0377fc;
    --background-color: #99c3f3;
    --background-color-light: #e2f1ff;
    --btn-hover: #0390fc;
    --border-color: blue;
    --text-main: black;
    --text-secondary: white;
    --title-color: -webkit-linear-gradient(-45deg, rgba(3, 119, 252, 0.78), #274685);
    --border-radius-btn: 25px;
    --color-red: #b40b0b;
    --color-grey: hsl(213, 30%, 29%);
    --color-grey-light: hsl(213, 30%, 59%);
}
.level_wrapper {
    display: flex;
    justify-content: center;
    margin-top: 2%;
    grid-gap: 1%;
    gap: 1%;
}
.home_wrapper {
    text-align: center;
}

.home_title {
    font-size: 50px;
}

.games_container {
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
    margin-top: 7%;
    justify-items: center;
}

.btn_game {
    width: 80%;
    display: flex;
    flex-direction: column;
    align-items: center;
    border: none;
    background-color: transparent;
}

.btn_game:hover {
    cursor: pointer;
    color: blue;
    font-weight: bold;
}

.img_game {
    width: 100%;
}

.text_game {
    font-size: 25px;
}

@media screen and (max-width: 768px) {
    .games_container {
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    .btn_game {
        width: 100%;
    }

    .text_game {
        margin-bottom: 10%;
    }
}

@media screen and (max-width: 480px) {
    .home_page_title_mob {
        font-size: 35px !important;
    }
}
.title_wrapper {
    display: flex;
    justify-content: center;
}

.game_title {
    font-size: 55px;
    background: var(--title-color);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 0 !important;
    line-height: 1.5 !important;
}

.emoji_icon {
    font-size: 50px;
}

@media screen and (max-width: 585px) {
    .title_wrapper {
        flex-direction: column;
    }
}

@media screen and (max-width: 480px) {
    .emoji_icon {
        font-size: 35px;
    }

    .game_title {
        font-size: 45px;
        line-height: 1.2 !important;
        margin-bottom: 5% !important;
    }
}
.card_list {
    margin: 2% 0 5% 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.memory_game_info {
    color: var(--text-main);
    font-size: 18px;
}

.cards_list_wrapper {
    display: grid;
    width: 60%;
    min-width: 300px;
    align-items: center;
    grid-template-columns: 32% 32% 32%;
    justify-content: space-around;
    margin-top: 2%;
    border: 1px solid var(--border-color);
    border-radius: var(--border-radius-btn);
    background-color: var(--background-color);
}

.cards_list_wrapper_middle {
    grid-template-columns: 23% 23% 23% 23%;
}

.cards_list_wrapper_hard {
    grid-template-columns: 19% 19% 19% 19% 19%;
}

@media screen and (max-width: 1024px) {
    .cards_list_wrapper {
        width: 80%;
    }
}

@media screen and (max-width: 768px) {
    .cards_list_wrapper {
        width: 95%;
        grid-template-columns: 47% 47%;
    }

    .cards_list_wrapper_middle {
        grid-template-columns: 30% 30% 30%;
    }

    .cards_list_wrapper_hard {
        grid-template-columns: 23% 23% 23% 23%
    }
}

@media screen and (max-width: 540px) {
    .cards_list_wrapper_middle {
        grid-template-columns: 47% 47%;
    }

    .cards_list_wrapper_hard {
        grid-template-columns: 47% 47%;
    }
}
.card_wrapper {
    display: flex;
    height: 200px;
    border: 1px solid var(--border-color);
    align-items: center;
    justify-content: center;
    margin: 4% 0;
    border-radius: var(--border-radius-btn);
    background-color: var(--btn-hover);
    -webkit-animation: 2s hideCard linear;
            animation: 2s hideCard linear;
}

.card_wrapper_middle {
    height: 160px;
}

.card_wrapper_hard {
    height: 120px;
}

.card_img {
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius-btn);
    opacity: 0;
    -webkit-animation: 2s hideImage linear;
            animation: 2s hideImage linear;
}

.card_wrapper.active .card_img {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.card_wrapper.active {
    -webkit-transform: rotateY(0);
            transform: rotateY(0);
}

@-webkit-keyframes hideCard {
    0%, 70% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
    100% {
        -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
    }
}

@keyframes hideCard {
    0%, 70% {
        -webkit-transform: rotateX(0deg);
                transform: rotateX(0deg);
    }
    100% {
        -webkit-transform: rotateX(180deg);
                transform: rotateX(180deg);
    }
}

@-webkit-keyframes hideImage {
    0%, 84% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
}

@keyframes hideImage {
    0%, 84% {
        opacity: 1;
    }
    85% {
        opacity: 0;
    }
}

@media screen and (max-width: 540px) {
    .card_wrapper {
        height: 180px;
    }
}

.timer_wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.timer {
    padding: 30px 0px;
    text-align: center;
}

.timer .time {
    background: var(--btn-hover);
    color: #fff;
    display: inline-block;
    font-family: monospace;
    font-size: 23px;
    padding: 13px;
    border-radius: 15px;
    width: 55px;
}

.timer .time_division {
    font-weight: bold;
    font-size: 25px;
    color: var(--btn-background);
}

.timer_button {
    background-color: var(--btn-background);
    border: 1px solid var(--border-color);
    border-radius: 15%;
    width: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.timer_icon {
    width: 30px;
    height: 30px;
    color: white;
}
.memory_game_wrapper {
    text-align: center;
}

.memory_game_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1%;
    justify-items: center;
}

.modal {
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    visibility: hidden;
    z-index: 11;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
    visibility: visible;
}

.modal_content {
    display: flex;
    flex-direction: column;
    background-color: white;
    border: 2px solid var(--btn-background);
    border-radius: 35px;
    font-size: 18px;
    padding: 20px;
    width: 60%;
    height: 60%;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    transition: 0.4s all;
    overflow: auto;
    z-index: 12;
}

.modal_content.active {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.close_icon_wrapper {
    cursor: pointer;
    width: 100%;
}

.close_icon {
    color: var(--color-red);
    float: right;
    width: 25px;
    height: 25px;
}

@media screen and (max-width: 540px) {
    .modal_content {
        width: 95%;
        height: 80%;
    }
}
.game_rules_title {
    font-size: 35px !important;
}

.game_rules_container {
    margin-top: 3%;
    text-align: start;
    list-style: "\1F9E0";
}

.game_rules_container li:hover {
    list-style: "\1F92F";
}
.game_btn {
    width: 25%;
    height: 50px;
    margin: 1% 0 0 0;
    font-size: 18px;
    background-color: var(--btn-background);
    border: 1px solid var(--border-color);
    color: var(--text-secondary);
    border-radius: var(--border-radius-btn);
}

.game_btn:hover {
    cursor: pointer;
    background-color: var(--btn-hover);
    border: 1px solid var(--btn-background);
}

@media screen and (max-width: 540px) {
    .game_btn {
        width: 60%;
    }
}
.sudoku_wrapper {
    text-align: center;
}

.sudoku_container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3%;
    justify-items: center;
}

.sudoku_btn {
    width: 20%;
    height: 35px;
    margin: 1% 0 0 0;
    background-color: deepskyblue;
    border: 1px solid blue;
}

.sudoku_btn:hover {
    cursor: pointer;
}

@media screen and (max-width: 768px) {
    .sudoku_wrapper {
        margin-bottom: 10%;
    }
}
.sudoku_desk_wrapper {
    display: grid;
    width: 80%;
    grid-template-columns: 50% 50%;
    justify-items: center;
    color: grey;
    margin: 0 10%;
}

.sudoku_all_tools {
    display: flex;
    width: 90%;
    flex-direction: column;
    justify-content: center;
}

.mob_timer {
    display: none;
}

@media screen and (max-width: 768px) {
    .sudoku_desk_wrapper {
        display: flex;
        flex-direction: column;
    }

    .sudoku_all_tools {
        width: 100%;
    }

    .mob_timer {
        display: flex;
        justify-content: center;
    }

    .web_timer {
        display: none;
    }
}
.game {
    display: flex;
    justify-items: center;
    justify-content: center;
}

.game__board {
    font-family: 'Noto Sans', sans-serif;
    font-size: 26px;
    margin: 30px 0;
    border: 2px solid var(--color-grey);
    border-collapse: collapse;
}

.game__row:nth-child(3n) {
    border-bottom: 2px solid var(--color-grey);
}

.game__cell {
    border: 1px solid var(--color-grey-light);
    padding: 0 14px;
    color: transparent;
    cursor: pointer;
}

.game__cell.game__cell--filled {
    color: var(--text-main);
}

.game__cell.game__cell--userfilled {
    color: var(--color-grey-light);
}

.game__cell.game__cell--selected {
    color: var(--background-color);
    background-color: var(--background-color);
}

.game__cell.game__cell--filled.game__cell--selected {
    color: var(--text-main);
    background-color: var(--background-color);
}

.game__cell.game__cell--userfilled.game__cell--selected {
    color: var(--color-grey-light);
    background-color: var(--background-color);
}

.game__cell.game__cell--highlightselected {
    color: var(--background-color);
    background-color: var(--background-color);
}

.game__cell.game__cell--filled.game__cell--highlightselected {
    color: var(--text-main);
    background-color: var(--background-color);
}

.game__cell.game__cell--userfilled.game__cell--highlightselected {
    color: var(--color-grey-light);
    background-color: var(--background-color);
}

.game__cell:nth-child(3n) {
    border-right: 2px solid var(--color-grey);
}

.game__cell.red {
    color: var(--color-red) !important;
}

.row__selected {
    color: var(--background-color-light);
    background-color: var(--background-color-light);
}

.same_num {
    background-color: var(--background-color);
}

@media screen and (max-width: 768px) {
    .game__cell {
        padding: 10px 12px;
    }
}



.status__numbers {
